<template>
  <v-form ref="form" v-model="valid" @submit.prevent="onSubmit">
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-text-field
            label="Brand"
            placeholder="Brand"
            v-model="arm.brand"
            :rules="rules.brand"
            autofocus
          />

          <v-text-field
            label="Font/Energy"
            placeholder="Font/Energy"
            type="number"
            v-model="arm.font_per_energy"
            :rules="rules.font_per_energy"
          />
        </v-col>
        <v-col cols="6">
          <v-text-field label="Model" placeholder="Model" v-model="arm.model" :rules="rules.model" />
          <v-text-field
            label="Length"
            placeholder="Length"
            type="number"
            v-model="arm.length"
            :rules="rules.length"
          />
        </v-col>
        <v-col cols="12">
          <v-textarea label="Comment" placeholder="Comment" v-model="arm.comment"></v-textarea>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <v-btn type="submit" color="primary" class="white--text mr-4" :disabled="!valid">Save</v-btn>
          <v-btn class="mr-4" @click="onCancel">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { required, isNumber } from "@/libs/validations";

export default {
  props: ["item"],
  mounted() {
    if (this.item) this.arm = { ...this.item };
  },
  data() {
    return {
      valid: false,
      arm: {
        brand: "",
        model: "",
        font_per_energy: "",
        length: "",
        comment: ""
      },
      rules: {
        brand: [required],
        model: [required],
        font_per_energy: [required, isNumber],
        length: [required, isNumber]
      }
    };
  },
  methods: {
    async onSubmit() {
      this.$emit("onSubmit", { ...this.arm });
      this.resetForm();
    },

    async onCancel() {
      this.$emit("onCancel");
      this.resetForm();
    },

    resetForm() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>
